// Bu modle presentation logic iin yaratlmtr. Ana modul View modldr bunlar onun altndaki 
//modller/classlar olacak

import {Fraction} from "fractional"

import View from "./View.js"

// Aada recipe markupnda ikonlar hala eski folder gsteriyor bu yzden onlar dzenlememiz lazm
// ncelikle icons.svg dosyasn bu dosyaya import ettik parcel sayesinde.
import icons from "url:../../img/icons.svg";




// Sonradan bunu view klasnn iine alacaz. Her bu tr viewlerin hepsi kendine ait baz methodlara
// sahip olacak bu yzden burda class kullanmak en mantkls.
 class RecipeView extends View {

    // parent olacak recipe setik nk recipe iin kontainer bu klass da zaten recipeview atand 
    // iin olduka tutarl bir mantk, ayrca spinner vs iinde bu parent kullanacaz.
    _parentElement = document.querySelector('.recipe');
    _errorMessage = "We could not find that recipe. Please try another one!";
    _message = "";






    // Show recipe fonksiyonu bize aslnda reicpe gsteriyor. Bu fonksiypnu belli addeventlistenera ekledik.  
    // Resultlar link iinde hash kodlarna sahip. Bunlara her tkladnda hash kodunu deitiriyorlar ve bizde bu deiime event ekleyerek showrecipe call back function aryor. Ayn ekilde sayfa yklenirken de yapyoruz.
    // Load eventi ise eer ayn linki alp baka sayfaya koyarsak showrecipe arlmyor nk has deimedi. 
    // bu yzden bizde ayrca load eventine de showrecipe aryoruz.

    // Bu method publisher and needs to access to subscriber
    addHandlerRender(handler) {
      window.addEventListener("hashchange", handler);
      window.addEventListener("load", handler);
    }

    addHandlerUpdateServings(handler) {
      this._parentElement.addEventListener("click",function(e) {
        const btn = e.target.closest(".btn--update-servings");
        if(!btn) return;
        const {updateTo} = btn.dataset
        if(+updateTo > 0) handler(+updateTo);
      })
    }

    addHandlerAddBookmark(handler) {
      this._parentElement.addEventListener("click", function(e) {
        const btn = e.target.closest(".btn--bookmark")
        if(!btn) return;
        handler();

      })
    }




    // Bu fonksiyonun direk return etmesini istiyoruz bu yzden return'e ilgili dom u yerletirdik.
    // Biz fonksiyonu render iinde altaraz bu yzden recipe datasnn olduu path arguman olarak 
    // render iine yerletirilcek ve bizde ordan ekerek alttaki koda ekleyeceimi bu yzden
    // this.#data yazdk nk renderin ilk kodunda recipe pathini direk bu dataya assign ettik.
    _generateMarkup() {
        
        // Recipe objesini hmtl yedirmek iin bunu yapyoruz. RENDER deniyor.
        // sonra bunu injectHTML methodu ile HTML yollucaz.
        return  `
        <figure class="recipe__fig">
            <img src="${this._data.image}" alt="${this._data.title}" class="recipe__img" />
            <h1 class="recipe__title">
              <span>${this._data.title}</span>
            </h1>
        </figure>
        
        <div class="recipe__details">
          <div class="recipe__info">
            <svg class="recipe__info-icon">
              <use href="${icons}#icon-clock"></use>
            </svg>
            <span class="recipe__info-data recipe__info-data--minutes">${this._data.cookingTime}</span>
            <span class="recipe__info-text">minutes</span>
          </div>
          <div class="recipe__info">
            <svg class="recipe__info-icon">
              <use href="${icons}#icon-users"></use>
            </svg>
            <span class="recipe__info-data recipe__info-data--people">${this._data.servings}</span>
            <span class="recipe__info-text">servings</span>
        
            <div class="recipe__info-buttons">
              <button class="btn--tiny btn--update-servings" data-update-to="${this._data.servings - 1}">
                <svg>
                  <use href="${icons}#icon-minus-circle"></use>
                </svg>
              </button>
              <button class="btn--tiny btn--update-servings" data-update-to="${this._data.servings + 1}">
                <svg>
                  <use href="${icons}#icon-plus-circle"></use>
                </svg>
              </button>
            </div>
          </div>
        
          <div class="recipe__user-generated ${this._data.key ? "" : "hidden"}">
             <svg>
               <use href="${icons}#icon-user"></use>
             </svg>
          </div>
          <button class="btn--round btn--bookmark">
            <svg class="">
              <use href="${icons}#icon-bookmark${this._data.bookmarked ? "-fill" : "" }"></use>
            </svg>
          </button>
        </div>
        
        <div class="recipe__ingredients">
          <h2 class="heading--2">Recipe ingredients</h2>
          <ul class="recipe__ingredient-list">
           
            ${this._data.ingredients.map(this._generateMarkupIngredient).join("")}
          
          </ul>
        </div>
        
        <div class="recipe__directions">
          <h2 class="heading--2">How to cook it</h2>
          <p class="recipe__directions-text">
            This recipe was carefully designed and tested by
            <span class="recipe__publisher">${this._data.publisher}</span>. Please check out
            directions at their website.
          </p>
          <a
            class="btn--small recipe__btn"
            href="${this._data.sourceUrl}"
            target="_blank"
          >
            <span>Directions</span>
            <svg class="search__icon">
              <use href="${icons}#icon-arrow-right"></use>
            </svg>
          </a>
        </div>
        `;
         
    }

    // Burda ingredientlar ayr bir yere aldk, bunu stte map methodunun iine atyoruz.
    _generateMarkupIngredient(ing)  {
      return ` 
         <li class="recipe__ingredient">
            <svg class="recipe__icon">
              <use href="${icons}#icon-check"></use>
            </svg>
            <div class="recipe__quantity">${ing.quantity ? new Fraction(ing.quantity).toString() : ""}</div>
            <div class="recipe__description">
              <span class="recipe__unit">${ing.unit}</span>
              ${ing.description}
            </div>
         </li>
      `
    }


}


// we dont pass any data in so therefore we dont need any contructe even. Bu exportu tamamen yukarda bu class iin tanmladmz methodlar controller da kullanmak iin yapyoruz.
export default new RecipeView() 