
// Yine bir view dosyas yarattk dzen iin. Buras search ksmndan sorumlu olacak. Bir instaance yaratp bunu contreller export ettik. 

// ncelikle ilgili container bulup bunu parent element olarak ekledik. Ardndan submit ksmndaki value almak iin getquery methodunu yarattk. 

class SearchView {
    _parentEl = document.querySelector(".search");


    getQuery() {
        const query =  this._parentEl.querySelector(".search__field").value;
        this._clearInput();
        return query;
    }

    _clearInput() {
        this._parentEl.querySelector(".search__field").value = "";
    }


    addHandlerSearch(handler) {
        this._parentEl.addEventListener("submit", function(e){
            e.preventDefault();
            handler();
        })
    }

}

export default new SearchView();
