// Aada recipe markupnda ikonlar hala eski folder gsteriyor bu yzden onlar dzenlememiz lazm
// ncelikle icons.svg dosyasn bu dosyaya import ettik parcel sayesinde.
import icons from "url:../../img/icons.svg";



export default class View {

    _data;


    // public metodlara ekliyoruz burda render. Bu methodun argumanna dzenlenmi/ekilmi recipe datasn
    // koymamz gerekiyor onun uanki lokasyonu buras:  model.state.recipe. Bu method controller da 
    // arlrken bu path koyulorak arlyor. 
    render(data, render = true) {
        if(!data || (Array.isArray(data) && data.length === 0)) return this.renderError();
        
        // Aada ilk satr ile bu pathi yukardaki public olan #data ya assign ediyoruz.
        // this class iindeki eyler kullanmak iin gerkeiyor.. 
        this._data = data;

        
        // burdada nce this ile bu clasa gelip ordan generatemarup methodunu aroyruz. Direk ara
        //myoruz nk bu fonksiyon rendern iinde deil.
        const markup = this._generateMarkup();

        if(!render) return markup;



        // markup yerletirmeden nce parent elementin iini sildik.
        this._clear();


        // Burda aadaki generateMarkup HTML kodunu html'ye yolluyoruz. ncelikle bu kodun iinde 
        // bulunmasn istediimiz parent elementi seiyoruz ki burda #parentElement ana class da belirtilmi.
        // Sonra insertadjacentHTML methodu uyguluyoruz. afterbegin ile firstchild of parent element     
        // yapyoruz ve sonrada iine atmay istediimiz deikeni ki burda generatemarkup oluyor methodu 
        // oluyor  
        this._parentElement.insertAdjacentHTML("afterbegin", markup)
    } 


    update(data) {
      this._data = data;
      const newMarkup = this._generateMarkup();


      // array from datay array yapmak iin kullanld.
      const newDOM = document.createRange().createContextualFragment(newMarkup);
      const newElements = Array.from(newDOM.querySelectorAll("*"));
      const curElements = Array.from(this._parentElement.querySelectorAll("*"));

      newElements.forEach((newEl, i) => {
        const curEl = curElements[i];

        // update changed text
        if(!newEl.isEqualNode(curEl) && newEl.firstChild?.nodeValue.trim() !== "") {
          curEl.textContent = newEl.textContent
        }

        // update changed attributes
        if(!newEl.isEqualNode(curEl)) {
          Array.from(newEl.attributes).forEach(attr => curEl.setAttribute(attr.name, attr.value));
          
        }
  
      });
    }

    // parent elementin iindeki eyleri silme fonksiyonu yaratk, bunu render da kullanacaz.
    _clear() {
        this._parentElement.innerHTML = "";
    }


    // Burda aslnda fetch den data gelirken bir spinner dndrmek istiyoruz bu yzden bunu yaptk.
    // aslnda bu css animation sadece html ekliyoruz istediimiz elementlere. Bunu controller.js 
    // de apprmamz lazm fetch den data gelmeden nce.
    renderSpinner() {
        const markup = `
          <div class="spinner">
            <svg>
              <use href="${icons}#icon-loader"></use>
            </svg>
          </div>
          `
        this._clear();
        this._parentElement.insertAdjacentHTML("afterbegin", markup)
  }


    renderError(message = this._errorMessage) {
      const markup = `
            <div class="error">
              <div>
                <svg>
                  <use href="${icons}#icon-alert-triangle"></use>
                </svg>
              </div>
              <p>${message}</p>
            </div>`
  
      this._clear();
      this._parentElement.insertAdjacentHTML("afterbegin", markup)

  }


  renderMessage(message = this._message) {
    const markup = `
          <div class="message">
            <div>
              <svg>
                <use href="${icons}#icon-smile"></use>
              </svg>
            </div>
            <p>${message}</p>
          </div>`

    this._clear();
    this._parentElement.insertAdjacentHTML("afterbegin", markup)

}


}