// Bu modl view ve model arasnda kpr vazifesi grmekte ve bir okestra efi gibi grev dalmlarn yapmakta ve ilgili handler dan sorumludur.


// recipeView den class ektik.
import recipeView from "./views/recipeView.js"

// Bu model rettiimiz eyler buraya ekmemizi salyor, state ve loadrecipe fonksiyonu gibi.
import * as model from "./model.js"

import { MODAL_CLOSE_SEC } from "./config.js"


// Bunlar polyfilin yani eski browserlarda almas iin aldk, onlar iin convert ediyorlar.
import "core-js/stable";
import "regenerator-runtime/runtime";



import searchView from "./views/searchView.js"
import resultsView from "./views/resultsView.js"
import bookmarksView from "./views/bookmarksView.js"
import paginationView from "./views/paginationView.js"
import addRecipeView from "./views/addRecipeView.js"



// if (module.hot) {
 // module.hot.accept();
//}


// https://forkify-api.herokuapp.com/v2

///////////////////////////////////////


// we use async function we will not block the main thread, it will work in background.
//  the asynchronous method returns a promise to supply the value at some point in the future.
const controlRecipes = async function () {
  try {

    // her result recipenin bir hash kodu var, linklere yerletirilmi. Tkladka hashler deiiyor
    // bizde bunlara addeventlistener ekledik ve her hash deitiridde almas iin..
    // hash deiirken bunu alp ayrca fetch in iine koyduk nk her recipe ayr bir hash koduna sahip ve bu kodlara gre fetch ediyoruz.


    // bu alttaki kodu modal a almyoruz nk bu ksm projenin almas ile ilgili. business logic ile alakas yok.
    const id = window.location.hash.slice(1);


    // Guard close diyoruz buna eer id yok ise aadakileri altrma demek oluyor. Olduka kullanl 
    // iyi bilmekte fayda var.
    if(!id) return;
    
    
     // aada fetch data bunu override edene kadar spinner ekliyor. Css animationu aslnda
     // dom ile ieri atlyor. Aada data gelince, render methodu tekrardan ilgili elemnti 
     // iini boaltyor ve kendi markupn yerletiriyor..
     recipeView.renderSpinner();

  //////////////// ---------- 0) Update results view to mark selected result --------------////////
     resultsView.update(model.getSearchResultsPage());

    


  //////////////// ---------- 1) LOADING RECIPE --------------////////

    // fetch ksmn modal da yaptk. Alttaki fonksiyon fetct yapp datay dzenleyip, state.recipe kmsn 
    // ekliyor. Bu fonksiyon fetch yapt iin kodu bloklamamaz lazm o yzden await keyworudnu yine
    // ekliyoruz 
     await model.loadRecipe(id);
    // Pekala imdi yukardaki fonskiyon sadece state.recipe manipule etti. imdi o datay kullanmamz lazm.


  //////////////// ---------- 2) RENDERING RECIPE --------------////////

    // Bu metodu birazdan recipeView klasnda tanmlayacaz. smini render koyduk olduka common name 
    // bu iler iin. React'da falan da bu isimde kullanlyor.
     recipeView.render(model.state.recipe);


    
  }catch(err) {
    console.log(err);
    recipeView.renderError();
  }

};

// Bu fonksiyon submit ksmndan ilgili query , query deikenine assign ediyor. Ardndan loadsearchresults ile state.search.results ksmna sonular yerletiriyor. 
const controlSearchResults = async function() {
  try {

    resultsView.renderSpinner();
    // 1) Get Search Query
    const query = searchView.getQuery();
    if(!query) return;

    // 2) Load search results
    await model.loadSearchResults(query);

    // 3) Render Results 
    resultsView.render(model.getSearchResultsPage(1));

    // 4) Render initial pagination buttons
    paginationView.render(model.state.search)


// Updating bookmarks view.
    bookmarksView.update(model.state.bookmarks)

  }catch(err) {
    console.log(err);
  }
}


const controlPagination = function(goToPage) {

  // 1) Render New Results 
  resultsView.render(model.getSearchResultsPage(goToPage));

    // 3) Render Bookmarks
    bookmarksView.render(model.state.bookmarks); 

  // 4) Render New  pagination buttons
  paginationView.render(model.state.search)


}

const controlServings = function(newServings) {
  // update recipe servings (in state)
  model.updateServings(newServings) 

  // update view as well.
  // recipeView.render(model.state.recipe);
  recipeView.update(model.state.recipe);
}

const controlAddBookmark = function() {
  // 1 ) add or remove bookmark
  if(!model.state.recipe.bookmarked) model.addBookmark(model.state.recipe) 
  else model.deleteBookmark(model.state.recipe.id) 

  // 2) update recipe
  recipeView.update(model.state.recipe)


}


const controlBookmarks = function() {
  bookmarksView.render(model.state.bookmarks)
}

const controlAddRecipe = async function(newRecipe) {
  try{

    // spinner 
    addRecipeView.renderSpinner();

  // upload the new recipe data
     await model.uploadRecipe(newRecipe);
     console.log(model.state.recipe)

     // render recipe 
     recipeView.render(model.state.recipe);

     // success message
      addRecipeView.renderMessage()

    // render bookmark view
    bookmarksView.render(model.state.bookmarks);

    // change id in url 

    window.history.pushState(null, "", `#${model.state.recipe.id}`);

     // close form window 
     setTimeout(function() {
       addRecipeView.toggleWindow();
     }, MODAL_CLOSE_SEC * 1000)
    }catch(err) {
      console.log("----", err);
      addRecipeView.renderError(err.message);
    } 

}


// Bu ksm modl mant asndan olduka nemli. Addeventlistener lar burda tutamyoruz nk
// onlar view ile ilgili fakat tamamen viewe de alamyoruz nk controller larda ilgili. Bu 
// sebeple publisher-subscriber pattern diye method kullanyoruz. Ayrca ReciperView  contrller dan
// bir fonskiyon arlamaz. nk controller diye birer varlndan bile haberi yok bu sebeple
// controller recipevire den fonksiyon ekebilir ancak.
// zm u ekilde. RecipeView de bir method yaratyoruz. 
// ve bu method ilgili addevent leri gerekletiriyor. Bizde burdan bir fonksiyon yaratyoruz ve ona 
// callback function bir argument olarak veriyoruz. Burdada bu fonksiyon direk arlyor.
// iin z callback'in arguman olarak kullanlmas aslnda.
const init = function() {
bookmarksView.addHandlerRender(controlBookmarks)  
recipeView.addHandlerRender(controlRecipes);
recipeView.addHandlerUpdateServings(controlServings);
recipeView.addHandlerAddBookmark(controlAddBookmark);
searchView.addHandlerSearch(controlSearchResults);
paginationView.addHandlerClick(controlPagination);
addRecipeView.addHandlerUpload(controlAddRecipe);
}
init();



